@import url(https://fonts.googleapis.com/css?family=Roboto:300);
@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* TopNav */

.socialNetworks {
    width: 100%;
    height: 5rem;
    width: 100%;
    /* background-color: #1B2735; */
    /* opacity:0.9; */
    background-color: #1B2735;
}

nav.navigation {
    float: left;
    background-color: #1B2735;
    /* background-color: #1B2735; */
    width: 100%;
    /* opacity: 0.9; */
    position: fixed;
    bottom: 0;
}

div.container a,
div.containerNav a {
    float: right;
    color: whitesmoke;
    font: 2.5rem 'Raleway';
    margin: 0px 0.625rem;
    padding: 0.625rem 0.625rem;
    position: relative;
    cursor: pointer;
    text-decoration: none;
}

div.container a {
    margin-top: .5rem;
    float: right;
}

div.containerNav a {
    float: left;
}


/* Brackets go out */

div.brackets a:before,
div.brackets a:after {
    position: absolute;
    opacity: 0;
    font-size: 3.125rem;
    top: 0px;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}

div.brackets a:before {
    content: '<';
    left: -1rem;
    -webkit-transform: translateX(0.625rem);
            transform: translateX(0.625rem);
}

div.brackets a:after {
    content: '>';
    right: -1rem;
    -webkit-transform: translateX(-0.625rem);
            transform: translateX(-0.625rem);
}

div.brackets a:hover:before,
div.brackets a:hover:after {
    opacity: 1;
    -webkit-transform: translateX(0rem);
            transform: translateX(0rem);
}

@media screen and (max-width: 320px) {
    div.containerNav {
        margin-left: 0%;
    }
    a.navIcons {
        position: absolute;
        cursor: pointer;
        margin: 0;
        padding: 0;
    }
    div.containerNav a {
        text-decoration: none;
        font: 2rem 'Raleway';
        margin: 0px 0.1%;
        position: relative;
        cursor: pointer;
    }
    /* Brackets go out */
    .navigation {
        width: 100%;
        height: 5rem;
    }
    nav.navigation {
        margin-left: 0%;
    }
}

@media screen and (max-width: 425px) {
    div.containerNav a {
        float: left;
        justify-items: center;
        font: 2.3rem 'Raleway';
        position: relative;
        cursor: pointer;
        margin: 0px 0.1%;
    }
}

@media screen and (min-width: 769px) {
    div.container a {
        margin-right: 5rem;
    }
    div.containerNav a {
        margin-left: 3rem;
    }
}
.homePage {
    /* background-color: #465362; */
    max-height: 100vh;
    position: relative;
    background-color: rgb(229, 236, 229);
    overflow: hidden;
}

.lta-source {
    position: relative;
    text-transform: uppercase;
    letter-spacing: 0.4em;
    font-weight: 18.75rem;
    font-size: 0.12em;
    width: 24em;
    height: 24em;
    line-height: 23.5em;
    border: 0.3em solid #000;
    text-align: center;
}

.nameTitle{
margin-top: 10rem;
}

.nf {
    color: black;
    text-align: center;
    /* background-color: #EDE7D9; */
    font-family: 'Raleway', sans-serif;
    
    height: 100vh;

    /* margin-bottom: 0; */
}

.nf-r1 {
    color: #555;
    font-size: 1em;
    font-style: normal;
    font-weight: 50rem;
    line-height: 1.2em;
    position: relative;
    display: inline-block;
    margin-bottom: 2em;
}

.nf-r1::after {
    content: '';
    position: absolute;
    height: 0.0625rem;
    width: 90%;
    margin-left: 4%;
    margin-right: 4%;
    bottom: -0.5em;
    right: 0;
    left: 0;
}

.nf-r2 {
    font-family: 'Raleway', sans-serif;
    font-size: 3em;
    line-height: 1em;
}

.descpritionHome {
    font-size: .8rem;
    line-height: 1rem;
    margin-top: 2rem;
    margin-left: 10rem;
    margin-right: 10rem;
    width: 70rem;
}

@media screen and (max-width: 320px) {
    .nf {
        line-height: 3em;
        padding-top: 5%;
    }
    .nf-r1::after {
        bottom: -0.5em;
        /* background-color: #000; */
    }
    .nf-r1 {
        font-size: 1em;
    }
    .nf-r2 {
        font-size: 1.7em;
        margin-right: 3%;
    }
}

@media screen and (max-width: 425px) {
    .nf-r1 {
        font-size: 1em;
    }
    .nf-r2 {
        font-size: 1.7em;
        margin-right: 3%;
    }
}

@media screen and (min-width: 769px) {
    .lta-source {
        margin-left: 3rem;
    }
    .descpritionHome {
        font-size: 1.5rem;
        line-height: 2rem;
        margin-top: 2rem;
        margin-left: 1rem;
        margin-right: 1rem;
    }
}
