}

.box4:before {
    width: 0;
    height: 200%;
    position: absolute;
    top: 0;
    left: -250px;
    bottom: 0;
    -webkit-transform: skewX(-36deg);
            transform: skewX(-36deg);
    -webkit-transition: all .5s ease 0s;
    transition: all .5s ease 0s
}

.box4:hover:before {
    width: 200%;
}

.box4 img {
    width: 100%;
    height: auto
}

.box4 .box-content {
    width: 100%;
    height: 100%;
    padding-top: 20%;
    position: absolute;
    top: 0;
    left: 0;
    -webkit-transform: scale(0);
            transform: scale(0);
    -webkit-transition: all .3s ease 0s;
    transition: all .3s ease 0s
}

.box4 .icon {
    list-style: none;
    margin-top: 0;
}

.box4:hover .box-content {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgb(138, 137, 137, .5);
}

